<template>
  <div>
    <!-- <header class="header">
      <div class="btn-group">
        <el-button type="primary" size="default" @click="handleSave()">保存表单</el-button>
      </div>
    </header> -->
    <fm-making-form ref="makingform" style="height:90vh" preview generate-code>
      <template slot="action">
        <el-button type="text" icon="el-icon-upload" @click="handleSave">保存</el-button>
      </template>
    </fm-making-form>
  </div>

</template>

<script>
  export default {
    name: '',
    data() {
      return {
        isShowFormking: false,
        detail: {},
        jsonData: {},
        isLoading: true,
      }
    },
    mounted() {
      this.$nextTick(() => {
        if (this.$route.query.id) {
          this.handleFormReady();
        }

      })
    },
    methods: {
      async handleSave() {
        const json = this.$refs.makingform.getJSON()
        if (json.list.length === 0) return this.$message.error('请先设计表单')
        let data = await this.$prompt('请输入表单名称：', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputValue: this.detail.formname
          // inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
          // inputErrorMessage: '邮箱格式不正确'
        }).catch(d => 0);
        if (!data || data.action != "confirm") return
        if (!data.value) return this.$message.error('输入表单名称')
        let stringData = JSON.stringify(json);
        let resData = await this.$http.post('/form/saveForm', {
          type: 1,
          formdata: encodeURIComponent(stringData),
          formname: data.value,
          id: this.$route.query.id,
        })
        if (resData && resData.code === 1) {
          this.$message.success('保存成功！')
          // window.location.href = "about:blank";
          // window.close(); 
          this.$router.push({
            path: '/'
          })

        } else {
          // this.$message.error(resData.message)
        }

      },
      handleFormReady() {
        console.log(this.$route.query.id)
        if (!this.$route.query.id) return;
        this.$http.post('/form/getFormDetail', {
          id: this.$route.query.id,
        }).then(resdata => {
          if (!resdata || !resdata.data) return
          this.detail = resdata.data;
          let json = decodeURIComponent(this.detail.formdata)
          this.jsonData = JSON.parse(json)
          this.$refs.makingform.setJSON(this.jsonData);

        }).catch(d => this.isLoading = false)
      },
    }
  }
</script>

<style lang="less" scoped>
  .header {
    height: 50px;
    padding: 20px;
  }

  .btn-group {
    display: flex;
    float: right;
  }
</style>


